import React from 'react';
import axios from 'axios';
import { withStyles, createStyles } from '@material-ui/core/styles';
import {authContext} from "../Auth";
import Username from "./username";
import Password from "./password"
import {deepPurple} from "@material-ui/core/colors";

export const Context = React.createContext({});

const styles = theme => createStyles({
    container: {
        textAlign: 'center',
        width: '100%',
    },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
    },
    button: {
        margin: theme.spacing.unit,
        cursor: 'pointer',
    },
    leftIcon: {
        marginRight: theme.spacing.unit,
    },
    rightIcon: {
        marginLeft: theme.spacing.unit,
    },
    iconSmall: {
        fontSize: 20,
    },
    avatar: {
        margin: 10,
        width: 90,
        height: 90,
        color:'#fff',
        backgroundColor: deepPurple[500],
        fontSize: '35px',
    },
});

class Signin extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            username: "",
            password: "",
            passwordToggle:false,
            ipDetails: "",
            avatar: "FF",
            errorText:""
        };
        this.handleUsername = this.handleUsername.bind(this);
        this.handlePassword = this.handlePassword.bind(this);
        this.clearPasswordError = this.clearPasswordError.bind(this);
        this.togglePassword = this.togglePassword.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleAvatar = this.handleAvatar.bind(this);

        axios.get("http://api.ipstack.com/check?access_key=5990a130f9d59f70be57812db0174f65")
            .then(response => {
                // console.log(response.data)
                this.setState({
                    ipDetails:response.data
                })
            })
            .catch(error => {
                console.log(error)
            });
    }

    handleUsername(value){
        this.setState({
            username:value
        },() => {
            console.log("username change -> "+this.state.username);
        });
    }

    handleAvatar(value){
        this.setState({
            avatar:value.toUpperCase()
        });
    }

    togglePassword(){
        this.setState({
            passwordToggle: !this.state.passwordToggle
        });
    }

    handlePassword(value){
        this.setState({
            password:value
        },() => {
            console.log("Password change -> "+this.state.password);
        })
    }

    clearPasswordError(){
        if(this.state.errorText !== ""){
            this.setState({
                errorText:""
            });
        }
    }

    handleSubmit(){
        if(this.state.password){
            axios.post("http://localhost:8080",{
                username:this.state.username,
                password:this.state.password,
                localization:navigator.language,
                ip_details:this.state.ipDetails,
            })
                .then(response => {
                    alert("Token Generated :\n"+response.data.token);
                })
                .catch(error => {
                    // alert(error.response.data.err)
                    this.setState({
                        errorText:error.response.data.err
                    });
                })
        }
        else{
            // alert("Password is required")
            this.setState({
                errorText:"Password is required"
            });
        }
    }

    render() {
        return(
            <authContext.Consumer>
                {handleSignupToggle => {
                    return (
                        <Context.Provider value={{...this.state,
                            handleUsername:this.handleUsername,
                            handlePassword:this.handlePassword,
                            handleSubmit:this.handleSubmit,
                            togglePassword:this.togglePassword,
                            clearPasswordError:this.clearPasswordError,
                            handleSignupToggle:handleSignupToggle,
                            handleAvatar:this.handleAvatar,
                            classes:this.props.classes
                        }}>
                            {/*<Slide direction="left" in={true} mountOnEnter unmountOnExit>*/}
                            <div>
                                {/*<h1>Sign in</h1>*/}
                                {!this.state.passwordToggle && <Username />}
                                {this.state.passwordToggle && <Password/>}
                            </div>
                            {/*</Slide>*/}
                        </Context.Provider>
                    )
                }}
            </authContext.Consumer>
        )
    }
}

export default withStyles(styles)(Signin)