import React from 'react';
import {Context} from "./signin";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import {Avatar, Grid} from "@material-ui/core";
import Link from "@material-ui/core/Link";
import ArrowBackIcon from "@material-ui/icons/ArrowBack"
import Button from "@material-ui/core/Button";
import InputAdornment from "@material-ui/core/InputAdornment";
import IconButton from "@material-ui/core/IconButton";
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';

class Password extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            password:this.props.context.password,
            showPassword:false
        };
        this.handlePassword = this.handlePassword.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleShowPassword = this.handleShowPassword.bind(this);
    }

    handlePassword(e){
        var value = e.target.value;
        this.setState({
            password:value
        },() => {
            this.props.context.clearPasswordError();
        });
    }

    handleShowPassword(){
        this.setState({
            showPassword:!this.state.showPassword
        });
    }

    async handleSubmit(e){
            e.preventDefault();
            await (this.props.context.handlePassword(this.state.password));
            this.props.context.handleSubmit();
    }

    render(){
        return(
            <div className={this.props.context.classes.container}>
                <Grid
                    container
                    direction="column"
                    justify="space-evenly"
                    alignItems="center"
                >
                    <Avatar className={this.props.context.classes.avatar} >{this.props.context.avatar}</Avatar>
                    <Typography component="h2" variant="h6">
                        Welcome {this.props.context.username}
                    </Typography>
                </Grid>
                <form method="post" onSubmit={this.handleSubmit}>
                <TextField
                    id="password"
                    label="Password"
                    type={this.state.showPassword ? 'text' : 'password'}
                    name="password"
                    margin="dense"
                    variant="outlined"
                    autoFocus
                    fullWidth
                    error ={this.props.context.errorText.length === 0 ? false : true }
                    helperText={this.props.context.errorText}
                    value={this.state.password}
                    onChange={this.handlePassword}
                    InputProps={{
                        endAdornment: (
                            <InputAdornment position="end">
                                <IconButton
                                    aria-label="Toggle password visibility"
                                    onClick={this.handleShowPassword}
                                >
                                    {this.state.showPassword ? <VisibilityOff /> : <Visibility />}
                                </IconButton>
                            </InputAdornment>
                        ),
                    }}
                />
                </form>
                <Grid
                    container
                    direction="row"
                    justify="space-between"
                    alignItems="center">

                    <Link
                        className={this.props.context.classes.button}
                        onClick={this.props.context.togglePassword}>
                        <Grid
                            container
                            direction="row"
                            justify="space-between"
                            alignItems="center">
                        <ArrowBackIcon className={this.props.context.classes.iconSmall}/>
                        Back
                        </Grid>
                    </Link>

                    <Button
                        variant="outlined"
                        color="primary"
                        className={this.props.context.classes.button}
                        onClick={this.handleSubmit}>
                        Submit
                    </Button>

                </Grid>
            </div>
        )
    }
}


export default props => (
    <Context.Consumer>
        {state => <Password context={state}/>}
    </Context.Consumer>
)
