import React from 'react';
import axios from 'axios';
import {authContext} from "../Auth";
import {Grid} from "@material-ui/core";
import Link from "@material-ui/core/Link";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import Typography from "@material-ui/core/Typography";
import withStyles from "@material-ui/core/styles/withStyles";
import createStyles from "@material-ui/core/styles/createStyles";

const styles = theme => createStyles({
    container: {
        textAlign: 'center',
        width: '100%',
    },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
    },
    button: {
        margin: theme.spacing.unit,
        cursor: 'pointer',
    },
    leftIcon: {
        marginRight: theme.spacing.unit,
    },
    rightIcon: {
        marginLeft: theme.spacing.unit,
    },
    iconSmall: {
        fontSize: 20,
    },
});

class Signup extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            email:"",
            firstname:"",
            lastname:"",
            username:"",
            password:"",
            confPassword:"",
            emailErrorText:"",
            firstnameErrorText:"",
            lastnameErrorText:"",
            usernameErrorText:"",
            passwordErrorText:"",
            confirmPasswordErrorText:"",
            checkusernametoggle:true,
            checkemailtoggle:true
        }

        this.handleEmail = this.handleEmail.bind(this)
        this.handleFirstname = this.handleFirstname.bind(this)
        this.handleLastname = this.handleLastname.bind(this)
        this.handleUsername = this.handleUsername.bind(this)
        this.handlePassword = this.handlePassword.bind(this)
        this.handleConfPassword = this.handleConfPassword.bind(this)
        this.handleCreateAccount = this.handleCreateAccount.bind(this)
        this.checkUsername = this.checkUsername.bind(this)
        this.checkEmail = this.checkEmail.bind(this)
    }

    handleEmail(e){
        var value = e.target.value;
        this.setState({
            email:value,
            emailErrorText:"",
        });
    }

    handleFirstname(e){
        var value = e.target.value;
        this.setState({
            firstname:value,
            firstnameErrorText:"",
        });
    }

    handleLastname(e){
        var value = e.target.value;
        this.setState({
            lastname:value,
            lastnameErrorText:"",
        });
    }

    handleUsername(e){
        var value = e.target.value;
        this.setState({
            username:value,
            usernameErrorText:"",
        });
    }

    handlePassword(e){
        var value = e.target.value;
        this.setState({
            password:value,
            passwordErrorText:"",
        });
    }

    handleConfPassword(e){
        var value = e.target.value;
        this.setState({
            confPassword:value,
            confirmPasswordErrorText:"",
        });
    }

    checkUsername(){
        if(this.state.username !== ""){
            axios.post("http://localhost:8080/checkUsernameAvailability", this.state)
                .then(response => {
                    this.setState({
                        checkusernametoggle:false,
                        usernameErrorText:response.data.success
                    });
                })
                .catch(error => {
                    // alert(error.response.data.err);
                    this.setState({
                        checkusernametoggle:true,
                        usernameErrorText:error.response.data.err
                    });
                })
        }
    }

    checkEmail(){
        if(this.state.email !== ""){
            axios.post("http://localhost:8080/checkEmailAvailability", this.state)
                .then(response => {
                    this.setState({
                        checkemailtoggle:false,
                        emailErrorText:response.data.success
                    });
                })
                .catch(error => {
                    // alert(error.response.data.err);
                    this.setState({
                        checkemailtoggle:true,
                        emailErrorText:error.response.data.err
                    });
                })
        }
    }

    handleCreateAccount(){

        if(this.state.password === this.state.confPassword){
            axios.post("http://localhost:8080/signup",this.state)
                .then(response => {
                    console.log(response);
                    alert(response.data.success)
                })
                .catch(error => {
                    error.response.data.forEach((obj) => {
                        switch (obj.name) {
                            case "email":
                                this.setState({
                                    checkemailtoggle:true,
                                    emailErrorText:obj.err
                                });
                                break;
                            case "firstname":
                                this.setState({
                                    firstnameErrorText:obj.err
                                });
                                break;
                            case "lastname":
                                this.setState({
                                    lastnameErrorText:obj.err
                                });
                                break;
                            case "username":
                                this.setState({
                                    checkusernametoggle:true,
                                    usernameErrorText:obj.err
                                });
                                break;
                            case "password":
                                this.setState({
                                    passwordErrorText:obj.err,
                                    confirmPasswordErrorText:"Confirm "+obj.err
                                });
                                break;
                            default:
                                break;
                        }
                    });
                });
        }
        else{
            // alert("Password and confirm password not matching")
            if(this.state.confPassword === ""){
                this.setState({
                    confirmPasswordErrorText:"Confirm your password"
                });
            } else {
                this.setState({
                    confirmPasswordErrorText:"Confirm password not matching"
                });
            }
        }

    }

    render() {
        return (
            <authContext.Consumer>
                {handleSignupToggle => {
                    return (
                        <div className={this.props.classes.container}>
                            <Typography component="h1" variant="h6">
                                Create your FogFind Account
                            </Typography>

                            <Grid
                                container
                                direction="column"
                                justify="space-evenly"
                                alignItems="center"
                            >
                                <Grid
                                    container
                                    direction="row"
                                    justify="space-between"
                                    alignItems="center">

                                    <TextField
                                        id="firstname"
                                        label="Firstname"
                                        type="text"
                                        name="firstname"
                                        autoComplete="off"
                                        margin="dense"
                                        variant="outlined"
                                        error ={this.state.firstnameErrorText.length === 0 ? false : true }
                                        helperText={this.state.firstnameErrorText}
                                        value={this.state.firstname}
                                        onChange={this.handleFirstname}
                                    />
                                    <TextField
                                        id="lastname"
                                        label="Lastname"
                                        type="text"
                                        name="lastname"
                                        autoComplete="off"
                                        margin="dense"
                                        variant="outlined"
                                        error ={this.state.lastnameErrorText.length === 0 ? false : true }
                                        helperText={this.state.lastnameErrorText}
                                        value={this.state.lastname}
                                        onChange={this.handleLastname}
                                    />

                                </Grid>

                                <TextField
                                    id="username"
                                    label="Username"
                                    type="text"
                                    name="username"
                                    autoComplete="off"
                                    margin="dense"
                                    variant="outlined"
                                    error ={(!this.state.checkusernametoggle || this.state.usernameErrorText.length === 0) ? false : true }
                                    helperText={this.state.usernameErrorText}
                                    value={this.state.username}
                                    fullWidth
                                    onBlur={this.checkUsername}
                                    onChange={this.handleUsername}
                                />

                                <TextField
                                    id="email"
                                    label="Email"
                                    type="text"
                                    name="email"
                                    autoComplete="off"
                                    margin="dense"
                                    variant="outlined"
                                    error ={(!this.state.checkemailtoggle || this.state.emailErrorText.length === 0) ? false : true }
                                    helperText={this.state.emailErrorText}
                                    value={this.state.email}
                                    fullWidth
                                    onBlur={this.checkEmail}
                                    onChange={this.handleEmail}
                                />

                                <Grid
                                    container
                                    direction="row"
                                    justify="space-between"
                                    alignItems="center">

                                    <TextField
                                        id="password"
                                        label="Password"
                                        type="password"
                                        name="password"
                                        autoComplete="off"
                                        margin="dense"
                                        variant="outlined"
                                        error ={this.state.passwordErrorText.length === 0 ? false : true }
                                        helperText={this.state.passwordErrorText}
                                        value={this.state.password}
                                        onChange={this.handlePassword}
                                    />

                                    <TextField
                                        id="confpassword"
                                        label="Confirm"
                                        type="password"
                                        name="confpassword"
                                        autoComplete="off"
                                        margin="dense"
                                        variant="outlined"
                                        error ={this.state.confirmPasswordErrorText.length === 0 ? false : true }
                                        helperText={this.state.confirmPasswordErrorText}
                                        value={this.state.confPassword}
                                        onChange={this.handleConfPassword}
                                    />

                                </Grid>

                            </Grid>

                            <Grid
                                container
                                direction="row"
                                justify="space-between"
                                alignItems="center">
                                <Link
                                    className={this.props.classes.button}
                                    onClick={handleSignupToggle}>
                                    Have account?
                                </Link>

                                <Button
                                    variant="outlined"
                                    color="primary"
                                    className={this.props.classes.button}
                                    onClick={this.handleCreateAccount}>
                                    Create Account
                                </Button>
                            </Grid>

                        </div>
                    )
                }}
            </authContext.Consumer>
        );
    }
}

export default withStyles(styles)(Signup);