import React from 'react';
import {Context} from "./signin";
import axios from 'axios';
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward"
import Link from "@material-ui/core/Link";
import {Grid} from "@material-ui/core";
import Typography from "@material-ui/core/Typography";

class Username extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            username:this.props.context.username,
            errorText:""
        };

        this.handleUsername = this.handleUsername.bind(this);
        this.handleNext = this.handleNext.bind(this)
    }

    handleUsername(e){
        var value = e.target.value;
        this.setState({
            username:value,
            errorText:""
        },() => {

        });
    }

    handleNext(e){
        e.preventDefault();
        if(this.state.username){
            axios.post("http://localhost:8080/checkUsername", this.state)
                .then(response => {
                    this.props.context.handleUsername(response.data.username);
                    this.props.context.handleAvatar(response.data.avatar);
                    this.props.context.togglePassword();
                })
                .catch(error => {
                    this.setState({
                        errorText:error.response.data.err
                    });
                })
        }
        else{
            this.setState({
                errorText:"Username is required"
            });
        }
    }

    render() {
        return(
            <div className={this.props.context.classes.container}>
                <Typography component="h1" variant="h6">
                        Sign in
                </Typography>

                <Typography component="h6" variant="subtitle1">
                    using FogFind Account
                </Typography>

                <form method="post" onSubmit={this.handleNext}>
                    <TextField
                        id="username"
                        label="Username or Email"
                        type="text"
                        name="username"
                        margin="dense"
                        variant="outlined"
                        fullWidth
                        error ={this.state.errorText.length === 0 ? false : true }
                        helperText={this.state.errorText}
                        value={this.state.username}
                        onChange={this.handleUsername}
                    />
                </form>

                <Grid
                    container
                    direction="row"
                    justify="space-between"
                    alignItems="center">

                    <Link
                        className={this.props.context.classes.button}
                        onClick={this.props.context.handleSignupToggle}>
                        Create account
                    </Link>

                    <Button
                        variant="outlined"
                        color="primary"
                        className={this.props.context.classes.button}
                        onClick={this.handleNext}>
                        Next
                        <ArrowForwardIcon className={this.props.context.classes.rightIcon} />
                    </Button>

                </Grid>
            </div>
        )
    }
}

export default props => (
    <Context.Consumer>
        {state => <Username context={state} />}
    </Context.Consumer>
)
