import React from 'react';
import Signin from './components/signin';
import Signup from "./components/signup";
import CssBaseline from '@material-ui/core/CssBaseline'
import Paper from '@material-ui/core/Paper';
import { withStyles, createStyles } from '@material-ui/core/styles';

export const authContext = React.createContext({});

const styles = theme => createStyles({
  main: {
    width: 'auto',
    display: 'block', // Fix IE 11 issue.
    marginLeft: theme.spacing.unit * 3,
    marginRight: theme.spacing.unit * 3,
    [theme.breakpoints.up(400 + theme.spacing.unit * 3 * 2)]: {
      width: 450,
      marginLeft: 'auto',
      marginRight: 'auto',
    },
  },
  paper: {
    marginTop: theme.spacing.unit * 16,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    padding: `${theme.spacing.unit * 2}px ${theme.spacing.unit * 1}px ${theme.spacing.unit * 3}px`,
    borderRadius: 7,
  },
  avatar: {
    margin: theme.spacing.unit,
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%',
    marginTop: theme.spacing.unit,
    paddingLeft: theme.spacing.unit * 2,
    paddingRight: theme.spacing.unit * 2,
  },
});

class Auth extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      signupToggle:false
    }
    this.handleSignupToggle = this.handleSignupToggle.bind(this)
  }

  handleSignupToggle(){
    this.setState({
      signupToggle: !this.state.signupToggle
    });
  }

  render() {
    return (
        <authContext.Provider value={this.handleSignupToggle}>
          <main className={this.props.classes.main}>
            <CssBaseline />

            <Paper className={this.props.classes.paper} elevation={20} >
              <div className={this.props.classes.form}>
                {!this.state.signupToggle && <Signin />}
                {this.state.signupToggle && <Signup />}
              </div>
            </Paper>

          </main>
        </authContext.Provider>
    );
  }
}

export default withStyles(styles)(Auth);